@import url(https://fonts.googleapis.com/css2?family=Grandstander&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Grandstander&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merienda&family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Markazi+Text:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* --main-bg-color: #cb8655; */
  /* --primary-bg-color: #7e8e65; */
  --body-bg-color: #e8dfce;

  --primary-bg-color: #666999;
  --main-bg-color: #b2c1d3;
}

.App {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.searchBar {
  background-color: #b2c1d3;
  background-color: var(--main-bg-color);
  height: 2em;
  padding: 0.1em;
  display: flex;
  justify-content: space-around;
}
.searchBar button {
  height: 90%;
  text-transform: uppercase;

  border-radius: 10px;
  border: 1px;
  background-color: #b2c1d3;
  background-color: var(--main-bg-color);
  padding: 0.7em;
  justify-content: center;
  text-align: center;
  align-items: center;
  font-family: "Grandstander", cursive;
  font-size: 0.8em;
  cursor: pointer;
}
.searchBar button:hover {
  /* background-color: #7e8e65; */
  background-color: #666999;
}

.body {
  background-color: #e8dfce;
  background-color: var(--body-bg-color);

  display: flex;

  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  justify-items: center;
  flex: 1 1;
  overflow-y: scroll;
}
.body h3 {
  font-family: "Grandstander", cursive;
  margin: 2.5em;
  text-transform: uppercase;
}
.Cards {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}

.body button {
  height: 40px;
  border: #cb8655 solid 1px;
  border-radius: 20px;
  background-color: #b2c1d3;
  padding: 2px 40px;
  margin-bottom: 20px;
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .searchBar button {
    font-size: 0.6em;
    padding: 0px;
  }
  .body h3 {
    margin: 1em;
  }
}

.header {
  background-color: var(--primary-bg-color);
  height: 5em;
  font-family: "Grandstander", cursive;
  padding: 5px;

  display: flex;
  flex-direction: row;
}
.logo {
  width: 214px;
}
.logo svg {
  font-size: 4em;
}
.search svg {
  font-size: 2em;
}

.title {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
}
.title h1 {
  font-size: 2em;
}

.search {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}
.SearchByWord {
  margin-top: 0.5em;
}
.SearchByWord button {
  border: solid 1px;
  height: 1.5em;
  cursor: pointer;
  background-color: var(--main-bg-color);
}

@media screen and (max-width: 690px) {
  .title h4 {
    /* display: none; */
    font-size: 0.9em;
  }
  .SearchByWord {
    margin-top: 0em;
    width: 5em;
  }
  .SearchByWord ::-webkit-input-placeholder {
    font-size: 0.8em;
  }
  .SearchByWord :-ms-input-placeholder {
    font-size: 0.8em;
  }
  .SearchByWord ::-ms-input-placeholder {
    font-size: 0.8em;
  }
  .SearchByWord ::placeholder {
    font-size: 0.8em;
  }
  .header {
    padding: 0em;
  }

  .SearchByWord button {
    width: 6em;
  }
  .logo {
    width: 150px;
  }
}

.Card {
  background-color: rgba(244, 240, 242, 0.5);
  border: var(--main-bg-color) solid 1px;
  border-radius: 15px;
  width: 300px;
  margin: 20px;
  padding: 15px;
}
.Card img {
  border-radius: 15px;
  margin: 15px;
}
.Card p {
  font-family: "Markazi Text", serif;
  font-size: 1.5em;
}

.Card a {
  text-decoration: none;
  font-family: "Merienda", cursive;
  font-size: 1em;
}
.Card a:hover {
  font-size: 1.2em;
}

.loader {
  display: flex;
  justify-content: center;
}
.lds-default {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-default div {
  position: absolute;
  width: 6px;
  height: 6px;
  background: var(--primary-bg-color);
  border-radius: 50%;
  -webkit-animation: lds-default 1.2s linear infinite;
          animation: lds-default 1.2s linear infinite;
}
.lds-default div:nth-child(1) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  top: 37px;
  left: 66px;
}
.lds-default div:nth-child(2) {
  -webkit-animation-delay: -0.1s;
          animation-delay: -0.1s;
  top: 22px;
  left: 62px;
}
.lds-default div:nth-child(3) {
  -webkit-animation-delay: -0.2s;
          animation-delay: -0.2s;
  top: 11px;
  left: 52px;
}
.lds-default div:nth-child(4) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
  top: 7px;
  left: 37px;
}
.lds-default div:nth-child(5) {
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
  top: 11px;
  left: 22px;
}
.lds-default div:nth-child(6) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
  top: 22px;
  left: 11px;
}
.lds-default div:nth-child(7) {
  -webkit-animation-delay: -0.6s;
          animation-delay: -0.6s;
  top: 37px;
  left: 7px;
}
.lds-default div:nth-child(8) {
  -webkit-animation-delay: -0.7s;
          animation-delay: -0.7s;
  top: 52px;
  left: 11px;
}
.lds-default div:nth-child(9) {
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
  top: 62px;
  left: 22px;
}
.lds-default div:nth-child(10) {
  -webkit-animation-delay: -0.9s;
          animation-delay: -0.9s;
  top: 66px;
  left: 37px;
}
.lds-default div:nth-child(11) {
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
  top: 62px;
  left: 52px;
}
.lds-default div:nth-child(12) {
  -webkit-animation-delay: -1.1s;
          animation-delay: -1.1s;
  top: 52px;
  left: 62px;
}
@-webkit-keyframes lds-default {
  0%,
  20%,
  80%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}
@keyframes lds-default {
  0%,
  20%,
  80%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}

.error_data h2 {
  margin: 30px;
}

.footer {
  background-color: var(--primary-bg-color);
  font-family: "Grandstander", cursive;

  padding: 0.4em;
}
.redes {
  margin-left: 0px;
  display: table;   /* Allow the centering to work */
	margin: 0 auto;
}
.redes ul {

	list-style: none;
	padding-top: 0.5em;
}
.redes ul li{
  display: inline;
  margin: 0.5em;
  font-size: 1.5em;
}

